.hero-box-bg {
	background: linear-gradient(90deg, #0047AD 100.22%, #0E75E1 47.26%, #F3F5F8 32.15%)
}

@media (min-width: 1024px) {
	.hero-box-bg {
		background: linear-gradient(90deg, #0047AD 1.22%, #0E75E1 47.26%, #F3F5F8 32.15%)
	}
}
.active-tab {
	color: #ED2E26;
	font-weight: 700;
	border-bottom:3px solid #ED2E26;
}

@media (min-width:1024px) {
	.q-cta {
		width: 400px;
		margin-right: 30px;
	}
	.q-cta-2 {
		margin-left: 40px;
	}
}
@media (min-width:1350px) {
	.q-cta-2 {
		margin-left: 20px;
	}
}

.q-cta-2-text {
	margin-top: 0px;
}

@media (min-width:640px) {
	.q-cta-2-text {
		margin-top: 1.25rem;
	}
}

@media (min-width:1024px) {
	.q-cta-2-text {
		margin-top: 3rem;
	}
}

@media  screen and (min-width:640px) and (max-width:1024px) {
	.q-cta-grid {
		width:500px
	}
}